@use '../_mixins/breakpoints' as bp;
@use '../_functions/calculations' as *;
@use '../_mixins/layout' as *;

// Layouts for content
// -----------------------------------
.l-gutter {
  @include l-gutter;
}

.l-narrow {
  @include l-narrow;
}

.l-full {
  @include l-full;
}

[class*='l-gutter'] [class*='l-gutter'] {
  margin-left: 0;
  margin-right: 0;
}

:root {
  // Expose margin amounts for offset
  @include bp.bp-min(mobile) {
    --margin: 15px;
  }
  // Tablet
  @include bp.bp-min(tablet) {
    --margin: 40px;
  }
  // Small & Medium Desktop
  @include bp.bp-min(sm-desktop) {
    --margin: 60px;
  }
  // Large Desktop
  @include bp.bp-min(lg-desktop) {
    --margin: 120px;
  }
}

// Attach smooth for all scrollable elements
html {
  scroll-behavior: smooth;
}

.contains-sidebar-content {
  @include bp.bp-min(tablet) {
    flex-direction: column;
  }

  @include bp.bp-min(desktop) {
    flex-direction: row;
  }
}

// Arranging columns for 2-col layouts when in mobile view
.wp-block-columns.sidebar-layout {
  .sidebar-layout-sidebar {
    @include bp.bp-min(desktop) {
      padding-left: rem(30px);
    }

    @include bp.bp-min(lg-desktop) {
      padding-left: rem(40px);

      > div {
        border-left: rem(2px) solid var(--cloud-grey);
        padding-left: rem(60px);
      }
    }
  }

  @include bp.bp-max(desktop) {
    // Overriding WP default
    flex-wrap: wrap !important;

    .sidebar-layout-content,
    .sidebar-layout-sidebar {
      width: 100%;
      // Overriding WP default
      flex-basis: 100% !important;
    }
  }
}

main#wp--skip-link--target {
  // Removing whitespace between menu and header of templates
  margin-top: 0px !important;
}

.wp-block-columns {
  position: relative;
}

.cleardiv {
  clear: both;
}
