//------------------------------------
//  03 - Mixins
//------------------------------------
@forward './_mixins/animations';
@forward './_mixins/breakpoints';
@forward './_mixins/buttons';
@forward './_mixins/layout';
@forward './_mixins/list';
@forward './_mixins/typography';

// Broad element styling
@forward 'elements/_elements';

// Individual components
@forward './components/callout-link';
@forward './components/footer';
@forward './components/heading-block';
@forward './components/news-archives';
@forward './components/sidebar-menu';

// Page templates
@import 'templates/_templates';

// Set default full window width
:root {
  --windowWidth: 1620px;

  // On largest screens take windowWidth less container width
  // divide to get 1 side beyond container width, this is used
  // so that placement is correct for images beyond container
  --full-left: calc(calc(var(--windowWidth) - 1400px) / -2);
  --full-right: calc(calc(var(--windowWidth) - 1400px) / -2);
}

// Utility class for screen-reader only content
.a11y-visible {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
